export default {
  name: 'Banner',
  props: {
    images: {
      type: Object,
      required: true,
    },
    promo_title: {
      type: String,
      required: true,
    },
    promo_title_display: {
      type: Boolean,
      required: true,
    },
    promo_description: {
      type: String,
      default: '',
      required: false,
    },
    promo_join_button: {
      type: String,
      required: true,
    },
    promo_join_button_location: {
      type: String,
      default: 'hidden',
      required: false,
    },
    promo_join_button_redirect_url: {
      type: String,
      required: true,
    },
  },
  computed: {
    cssBackground: function () {
      return {
        '--bg-image': `url('${this.images.promo_bg_desktop}')`,
        '--bg-image-m': `url('${this.images.promo_bg_mobile}')`,
        '--bg-banner': `url('${this.images.promo_bg_banner}')`
      }
    }
  }
};
